@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";
.sitemap {
    .container {}

    .sitemap__wrapper {
        padding-top: 100px;
        display: flex;
        justify-content: start;
        align-items: center;

        ol {
            li {
                &:not(:last-child) {
                    margin-bottom: 10px;
                }

                a {
                    font-size: 20px;
                }
            }
        }
    }
}

.wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: #f6f6f7;
}
.header,
.footer {
    flex-shrink: 0;
}
.main {
    flex-grow: 1;

    &__wrapper {
        display: flex;
        position: relative;
    }
}
.container {
    max-width: 1110px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
}

.page-h1 {
    @include page-h1();
}
.page-h2 {
    @include page-h2();
}
.page-caption {
    @include page-caption();
}
.page-text {
    @include page-text();
}
.page-btn {
    @include page-btn();
}
.page-field {
    @include page-field();
}

body,
html {
    font-family: "SF-Pro-Display" !important;
    letter-spacing: 0.3px;
}
* {
    min-height: 0;
    min-width: 0;
    box-sizing: border-box;
}

.m {
    &-b {
        &-sm {
            margin-bottom: 8px;
        }
    }

    &-r {
        &-sm {
            margin-right: 8px;
        }

        &-md {
            margin-right: 16px;
        }
    }

    &-l {
        &-sm {
            margin-left: 8px;
        }

        &-md {
            margin-left: 16px;
        }
    }

    &-x {
        &-auto {
            margin: 0 auto;
        }

        &-sm {
            margin-left: 8px;
            margin-right: 8px;
        }
    }
}

.overflow-hidden {
    overflow: hidden;
}

.d {
    &-flex {
        display: flex;
    }
}

.justify-content {
    &-between {
        justify-content: space-between;
    }
}

.align-items {
    &-center {
        align-items: center;
    }
}

.flex {
    &-wrap {
        flex-wrap: wrap;
    }
}

.position {
    &-relative {
        position: relative;
    }
}

.title {
    &-big {
        font-size: 42px;
        font-weight: 500;
        line-height: 50px;
    }

    &-medium {
        font-size: 32px;

        @media screen and (max-width: 992px) {
            font-size: 26px;
        }
    }
}

.text {
    &-500 {
        font-weight: 500;
    }
    &-600 {
        font-weight: 600;
    }

    &-uppercase {
        text-transform: uppercase;
    }

    &-white {
        color: #ffffff;
    }

    &-gray {
        color: #c6c6c6;
    }

    &-green {
        color: #2a6631;
    }

    &-small {
        font-size: 14px;
    }

    &-regular {
        font-size: 16px;
    }

    &-medium {
        font-size: 18px;
    }

    &-big {
        font-size: 28px;
    }
}

.button {
    display: flex;

    cursor: pointer;

    &-outline {
        border: 1px solid #2a6631;
        padding: 10px 16px;
        background-color: transparent;
        border-radius: 24px;
    }

    &-green {
        background-color: #2a6631;
        padding: 16px 40px;
        border-radius: 12px;
    }
}

.header {
    padding: 20px 40px;
    box-sizing: border-box;
    width: 100%;
    z-index: 10;
    transition: 0.3s;
    border-bottom: 1px solid #eaedf7;
    background-color: #fff;

    @media screen and (max-width: 768px) {
        padding: 20px 16px;
    }

    &__burger {
        display: none;
        position: relative;
        padding: 7px 0;

        @media screen and (max-width: 768px) {
            display: block;
        }

        &.is-active {
            span {
                opacity: 0;
            }

            &::before,
            &::after {
                top: 50%;
            }

            &::before {
                transform: rotate(45deg);
                width: 100%;
            }

            &::after {
                transform: rotate(-45deg);
            }
        }

        span {
            transition: all 0.25s;
            position: relative;
            display: block;
            width: 20px;
            height: 2px;
            background-color: #000000;
        }

        &::before,
        &::after {
            content: "";
            transition: all 0.25s;
            display: block;
            position: absolute;
            background-color: #000000;
            height: 2px;
        }

        &::before {
            top: 0;
            width: 70%;
        }

        &::after {
            bottom: 0;
            width: 100%;
        }
    }

    .header__wrapper {
        display: flex;
        justify-content: end;
        align-items: center;

        @media screen and (max-width: 768px) {
            justify-content: space-between;
        }
    }

    &__profile {
        display: flex;
        align-items: center;

        &-icon {
            width: 40px;
            height: 40px;
            background-color: #bababa;
            border-radius: 50%;
            margin-left: 8px;
        }
    }

    .header__burger {
        cursor: pointer;

        &:hover {
            .header__burger-wrap {
                background: #0081cf;
                &::before,
                &::after {
                    border-color: #ffffff;
                }
            }
            .header__burger-item {
                border-color: #ffffff;
            }
        }
    }
    .header__burger-wrap {
        padding: 13px 10px;
        box-sizing: border-box;
        background: #e9f6fd;
        width: 40px;
        height: 40px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        transition: 0.3s;

        &::before,
        &::after {
            content: "";
            width: 100%;
            display: block;
            box-sizing: border-box;
            border: 1px solid #0092ea;
        }
    }
    .header__burger-item {
        border: 1px solid #0092ea;
        width: 100%;
        box-sizing: border-box;
    }
}

.sidebar {
    background-color: #ffffff;
    // width: 300px;
    flex-shrink: 0;
    transition: all 0.25s;
    z-index: 10;

    @media screen and (min-width: 1440px) {
        width: 300px;
    }

    @media screen and (max-width: 992px) {
        width: 60px;
    }

    @media screen and (max-width: 768px) {
        position: fixed;
        top: 81px;
        left: 0;
        height: calc(100vh - 81px);
        transform: translateX(-100%);
        width: 300px;
        max-width: 100%;
        overflow-y: scroll;

        &.is-active {
            transform: translateX(0);
        }
    }

    &__title {
        padding: 0 24px;
        margin-bottom: 16px;
        font-weight: 600;

        @media screen and (max-width: 992px) {
            display: none;
        }

        @media screen and (max-width: 768px) {
            display: inline-block;
        }
    }

    &__chapter {
        padding: 40px 0;

        & + .sidebar__chapter {
            border-top: 1px solid #eaedf7;
        }
    }

    &__list {
        width: 100%;

        &-item {
            display: flex;
            align-items: center;
            padding: 18px 24px;
            transition: all 0.25s;

            @media screen and (max-width: 992px) {
                padding: 18px;
            }

            cursor: pointer;

            &.is-active {
                background-color: #2a6631;

                .sidebar__list-item-icon {
                    fill: #ffffff;
                }

                span {
                    color: #ffffff;
                }
            }

            &:not(.is-active):hover {
                background-color: #f4f7f5;
            }

            span {
                display: inline-block;
                font-weight: 500;
                margin-left: 10px;

                @media screen and (max-width: 992px) {
                    display: none;
                }

                @media screen and (max-width: 768px) {
                    display: inline-block;
                }
            }

            &-icon {
                fill: #2a6631;
                flex-shrink: 0;
            }

            & + .sidebar__list-item {
                margin-top: 8px;
            }
        }
    }
}

.section {
    &__wrapper {
        padding: 40px;

        @media screen and (max-width: 375px) {
            padding: 40px 16px;
        }
    }

    &__title {
        @media screen and (max-width: 768px) {
            font-size: 26px;
            line-height: 30px;
        }
    }
}

.home {
    flex-grow: 1;
    background-color: #f6f6f7;

    &__info {
        display: flex;
        flex-wrap: wrap;
        margin-top: 80px;
        margin-bottom: 16px;

        @media screen and (max-width: 992px) {
            margin-top: 60px;
        }

        &-balance {
            padding: 24px;
            background-color: white;
            border-radius: 20px;
            width: 35%;

            @media screen and (min-width: 992px) {
                margin-right: 24px;
            }

            @media screen and (max-width: 992px) {
                width: 100%;
            }
        }

        &-referral {
            display: flex;
            background-color: #ffffff;
            border-radius: 20px;
            width: 60%;

            @media screen and (max-width: 992px) {
                width: 100%;
                margin-top: 24px;
            }

            @media screen and (max-width: 768px) {
                flex-direction: column;
            }

            &-inner {
                padding: 24px;
                width: 50%;

                @media screen and (max-width: 768px) {
                    width: 100%;
                }

                &:last-child {
                    background-color: #2a6631;
                    border-radius: 20px;
                }
            }
        }

        &-url {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-radius: 20px;
            width: 100%;
            background-color: #ffffff;
            padding: 24px;
            margin-top: 24px;

            cursor: pointer;

            p {
                overflow: hidden;
                text-overflow: ellipsis;
                width: 90%;

                @media screen and (max-width: 768px) {
                    font-size: 24px;
                }

                @media screen and (max-width: 375px) {
                    font-size: 16px;
                }
            }
        }

        &-text {
            margin-bottom: 100px;
            margin-left: 10px;
            margin-right: 10px;

            @media screen and (max-width: 992px) {
                margin-bottom: 80px;
            }
        }
    }

    &__table {
        padding-top: 40px;
        padding-bottom: 24px;
        padding-left: 24px;
        padding-right: 24px;
        background-color: #ffffff;

        @media screen and (min-width: 768px) {
            border-radius: 20px;
        }

        @media screen and (max-width: 768px) {
            margin: 0 -40px;
        }

        &-title {
            margin-bottom: 40px;
        }

        &-wrapper {
            position: relative;
            border-radius: 20px;
            background-color: #f7f8f8;
            padding: 16px 0;

            @media screen and (max-width: 992px) {
                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    right: 0;
                    height: 100%;
                    width: 40%;
                    background: linear-gradient(270deg, #f7f8f8 0%, rgba(247, 248, 248, 0) 100%);
                    user-select: none;
                    pointer-events: none;
                }
            }

            @media screen and (max-width: 768px) {
                margin-right: -24px;
                margin-left: -24px;
            }
        }

        &-rail {
            overflow-x: auto;
        }

        &-row {
            display: grid;
            gap: 24px;
            // grid-template-columns: 1fr 1fr 0.5fr 0.5fr 0.5fr;
            grid-template-columns: 320px 254px 98px 98px 98px;

            @media screen and (max-width: 768px) {
                grid-template-columns: 240px 240px 98px 98px 98px;
            }

            padding: 20px 24px;
            &:not(:first-child) {
                border-top: 1px solid #e3e3e3;
            }
        }

        &-element {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }

    &__notification {
        margin-top: 40px;
        padding: 24px;
        border-radius: 20px;
        background-color: #ffffff;

        &-wrapper {
            display: flex;
            justify-content: space-between;

            @media screen and (max-width: 992px) {
                flex-direction: column;
                justify-content: flex-start;
                align-items: start;

                .button {
                    margin-top: 16px;
                }
            }
        }

        &-counter {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            width: 24px;
            height: 24px;
            color: #ffffff;
            background-color: #ec5656;
            margin-left: 16px;
        }

        &-list {
            margin-top: 40px;
        }

        &-item {
            padding: 24px;
            border: 1px solid #f7f8f8;
            border-radius: 20px;

            @media screen and (max-width: 992px) {
                flex-direction: column-reverse;
            }

            &-name {
                position: relative;

                @media screen and (max-width: 992px) {
                    margin-top: 24px;
                }
            }

            &.is-active {
                background-color: #f7f8f8;

                .home__notification-item-name {
                    padding-left: 20px;

                    &::before {
                        content: "";
                        position: absolute;
                        display: block;
                        top: 5px;
                        left: 0;
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        background-color: #ec5656;
                    }
                }
            }

            & + .home__notification-item {
                margin-top: 24px;
            }
        }

        .button {
            margin-top: 24px;
        }
    }
}

