.input {
    outline: none;
    border: none;

    padding: 8px 13px;

    &__date {
        width: 64px;
        color: #c6c6c6;
        border-radius: 8px;
    }

    &__search {
        padding: 14px 16px;
        border: 1px solid #e3e3e3;
        width: 240px;
        border-radius: 12px;

        @media screen and (min-width: 992px) {
            margin-left: 24px;
        }

        @media screen and (max-width: 992px) {
            width: 92%;
        }
    }
}

.statistic {
    flex-grow: 1;
    background-color: #f6f6f7;
    overflow: hidden;

    .section__wrapper {
        @media screen and (max-width: 768px) {
            padding-left: 16px;
            padding-right: 16px;
        }
    }

    &__title {
        font-size: 32px;

        @media screen and (max-width: 768px) {
            font-size: 26px;
        }
    }

    &__subtitle {
        margin-top: 16px;
        opacity: 0.6;
        max-width: 570px;
        margin-bottom: 48px;
    }

    &__content {
        padding: 40px 24px 24px 24px;
        background-color: #fff;
        border-radius: 24px;

        @media screen and (max-width: 768px) {
            margin-left: -16px;
            margin-right: -16px;
        }

        &-date {
            display: flex;
            justify-content: space-between;

            padding: 14px 16px;
            background-color: #f7f8f8;
            border-radius: 12px;

            @media screen and (min-width: 992px) {
                width: 370px;
            }

            @media screen and (max-width: 992px) {
                width: 100%;
                order: 2;
                margin-top: 24px;
            }

            @media screen and (min-width: 768px) {
                align-items: center;
            }

            @media screen and (max-width: 768px) {
                flex-direction: column;

                &-elem {
                    &:first-child {
                        margin-bottom: 16px;
                    }
                }
            }
        }

        &-search {
            display: flex;
            align-items: center;

            @media screen and (max-width: 992px) {
                order: 1;
                flex-direction: row-reverse;
                justify-content: space-between;
                width: 100%;
            }
        }
    }

    &__table {
        padding-left: 0;
        padding-right: 0;

        .home__table-wrapper {
            @media screen and (max-width: 768px) {
                margin: 0 16px;
            }
        }
    }

    &__pagination {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;

        @media screen and (max-width: 768px) {
            margin-left: -16px;
            margin-right: -16px;
        }

        &-item,
        &-all {
            border: 1px solid #f7f8f8;
            border-radius: 8px;
        }

        &-item {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 32px;
            height: 32px;
            // border: 1px solid #F7F8F8;
            // border-radius: 8px;

            &.is-current {
                background-color: #f7f8f8;
            }

            & + .statistic__pagination-item {
                margin-left: 8px;
            }
        }

        &-all {
            padding: 7px 12px;

            span {
                margin-right: 4px;
            }
        }
    }
}

