.support {
    position: relative;
    flex-grow: 1;
    background-color: #f6f6f7;
    overflow: hidden;

    &__title {
        font-size: 32px;
        margin-bottom: 48px;

        @media screen and (max-width: 768px) {
            font-size: 26px;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        // &-list {
        //     display: flex;
        //     flex-direction: column;
        //     justify-content: space-between;
        // }

        &-block {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            background-color: #ffffff;
            width: 48%;
            height: 262px;
            border-radius: 20px;
            padding: 24px;

            @media screen and (max-width: 768px) {
                width: 100%;
                height: 242px;

                & + .support__content-block {
                    margin-top: 32px;
                }
            }
        }

        &-item {
            &:first-child {
                padding-bottom: 16px;
            }

            &:last-child {
                padding-top: 16px;
            }

            & + .support__content-item {
                border-top: 1px solid #e3e3e3;
            }

            &-info {
                font-size: 20px;
            }
        }
    }

    &__call {
        display: block;
        width: 48px;
        height: 48px;
        background-color: #0081cf;

        @media screen and (min-width: 992px) {
            position: absolute;
            right: 40px;
            bottom: 40px;
        }

        @media screen and (max-width: 992px) {
            margin-top: 48px;
        }
    }
}

