.change {
    position: relative;
    flex-grow: 1;
    background-color: #f6f6f7;
    overflow: hidden;

    &__title {
        font-size: 32px;
        margin-bottom: 40px;

        @media screen and (max-width: 768px) {
            font-size: 26px;
        }
    }

    &__chapter {
        margin-bottom: 40px;

        &-reqisite {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 16px 0;
            margin-bottom: 16px;
            border-top: 1px solid #e3e3e3;
            border-bottom: 1px solid #e3e3e3;

            &-item {
                &:last-child {
                    @media screen and (max-width: 768px) {
                        display: block;
                        width: 40%;
                        text-align: end;
                    }
                }
            }
        }

        &-label {
            margin-bottom: 16px;
        }

        &-wrapper {
            display: grid;
            gap: 24px;
            grid-template-columns: repeat(2, 1fr);

            &--2 {
                grid-template-columns: 1fr;
            }

            @media screen and (max-width: 992px) {
                grid-template-columns: 1fr;
            }
        }

        &-form {
        }

        &-field {
            // width: 48%;

            .home__info-field {
                font-weight: 400;
            }

            input {
                color: #c6c6c6;
                width: 100%;
                padding: 24px;
                border-radius: 20px;
            }

            @media screen and (max-width: 768px) {
                .home__info-span {
                    font-size: 14px;
                }
            }
        }
    }
}

