}
.acc {
    padding: 36px 24px;
    background: #FFFFFF;
    border-radius: 20px;

    &:not(:last-child) {
        margin-bottom: 16px;
    }
}
.acc-headline {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.acc__header {
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    
    color: #000000;

    display: flex;
    justify-content: start;
    align-items: center;
}
.count {
    background: #F4F7F5;
    border-radius: 500px;
    padding: 10px;

    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    
    /* identical to box height */
    
    color: #8D8D8D;  
    margin-left: 24px;  

    &.--white {
        background: #fff;
        color: #000;
        margin-left: 0;
    }
}
.acc__btn {
    width: 48px;
    height: 48px;
    box-sizing: border-box;
    border-radius: 50%;
    background: #F7F8F8;

    display: flex;
    justify-content: center;
    align-items: center;
    transition: .3s;
    flex-shrink: 0;

    &._active {
        transform: rotate(180deg);
    }
}
.acc-content {
    display: none;
    padding-top: 40px;
}
.acc__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;

    &.__2col {
        grid-template-columns: 1fr 1fr;

        .acc__item-name {
            font-size: 20px;
            line-height: 120%;  
            font-weight: 600;          
        }
    }
}
.acc__item {
    padding: 12px;
    box-sizing: border-box;
    background: #F7F8F8;
    border-radius: 16px;   
    
    display: flex;
    justify-content: start;
    align-items: flex-start;

}
.acc__item-img {
    max-width: 206px;
    height: 116px;
    width: 100%;
    margin-right: 24px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.acc__item-status {
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 140%;
    
    /* identical to box height, or 20px */
    letter-spacing: -0.02em;   
    
    &.--red {
        color: #EC5656;       
    }
    &.--green {
        color: #30B04C;        
    }
}
.acc__item-name {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    /* or 19px */
    color: #000000;
    margin-top: 8px;
}
.acc__item-nav {
    margin-top: 16px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}
.acc__item-desc {
    width: 100%;
}
.acc__item-link {
    display: flex;
    justify-content: start;
    align-items: center;

    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    /* Green/Main accent */
    color: #2A6631;  
    text-decoration: none;
    
    img {
        margin-left: 11px;
    }
}

.payment {
    overflow: hidden;
    .home__info {

    }
    .home__info-balance {
        width: 49%;
    }
    .home__info-referral {
        width: 49%;
    }
    .home__info-referral-inner:last-child {
        background: #fff;
    }

    .home__info-payment {
        margin: 41px 0 48px;
        width: 100%;
    }
    .home__info-header {
        font-weight: 510;
        font-size: 20px;
        line-height: 120%;
        
        color: #000000;   
        margin-bottom: 16px;     
    }
    .home__info-form {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .home__info-label {
        width: 100%;
        position: relative;
        display: block;
    }
    .home__info-field {
        font-style: normal;
        font-weight: 510;
        font-size: 16px;
        line-height: 19px;
        
        /* identical to box height */
        
        /* Main/Descriptor text */
        color: #C6C6C6;  

        display: block; 
        width: 100%; 
        
        background: #FFFFFF;
        border-radius: 20px;
        border: none;
        outline: none !important;
        
        padding: 20px 200px 20px 25px;
    }
    .home__info-span {
        font-style: normal;
        font-weight: 510;
        font-size: 16px;
        line-height: 19px;
        
        /* identical to box height */
        
        /* Green/Main accent */
        color: #2A6631; 
        
        position: absolute;
        top: 50%;
        right: 24px;
        transform: translateY(-50%);
    } 
    .home__info-submit {
        flex-shrink: 0;

        font-weight: 510;
        font-size: 16px;
        line-height: 19px;
        text-align: center;        
        /* Main/White */
        color: #FFFFFF;   
        padding: 20px 40px;  
        
        background: #2A6631;
        border-radius: 20px; 
        border: none;
        outline: none;   
        margin-left: 24px;    
    }
}
.home__table-rail.__3col .home__table-row {
    grid-template-columns: 305px 305px 147px;
}
.status {
    display: flex;
    justify-content: start;
    align-items: center;

    border-radius: 8px;
    padding: 11px 15px;

    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    
    color: #FFFFFF;    

    &.--yellow {
        background: rgba(236, 176, 86, 0.8);        
    }
    &.--red {
        background: rgba(236, 86, 86, 0.8);
    }
    &.--green {
        background: rgba(72, 210, 111, 0.8);
    }

    img {
        margin-right: 11px;
    }
}

.home__text-wrapper {
    margin-top: 48px;
    .home__text-par {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
        
        opacity: 0.6;  
        margin: 24px 0;      
    }
}
@media screen and (min-width: 992px) {
    .payment .home__info-balance {
        margin-right: 15px;
    }
}
@media screen and (max-width: 992px) {
    .payment .home__table-wrapper::after {
        display: none;
    }
    .home__table-rail.__3col .home__table-row {
        grid-template-columns: 1fr 1fr 147px;
    }
    .payment .home__info-balance {
        width: 100%;
    }
    .payment .home__info-referral {
        width: 100%;
    }
    .payment .home__info-form {
        flex-direction: column;
        align-items: flex-start;
    }
    .payment .home__info-submit {
        width: 100%;
        margin-left: 0;
        margin-top: 16px;
    }
}
@media screen and (max-width: 768px) {
    .acc__header {
        flex-direction: column;
        align-items: flex-start;
    }
    .count {
        margin-left: 0;
        margin-top: 8px;
    }
    .acc__list {
        grid-template-columns: 1fr !important;
    }
    .acc-headline {
        align-items: flex-start;
    }
    .payment .home__table-wrapper {
        margin-right: -10px;
        margin-left: -10px;
    }
}
@media screen and (max-width: 568px) {
    .acc {
        padding: 16px;
        border-radius: 20px;        
    }
    .acc__header {
        font-size: 16px;
    }
    .acc__item {
        flex-direction: column;
    }
    .acc__item-img {
        margin-right: 0;
        margin-bottom: 12px;
    }
    .acc__item-img {
        max-width: 100%;
        height: auto;
    }
}
