@mixin page-h1() {
    font-weight: 500;
    font-size: 42px;
    line-height: 140%;
    
    /* Text [day]/Primary */
    color: $header-color;

    @media screen and (max-width: 1110px) {
        font-weight: 600;
        font-size: 32px;
        line-height: 140%;        
    }
    @media screen and (max-width: 675px) {
        font-weight: 600;
        font-size: 25px;
        line-height: 140%;
    }
}

@mixin page-h2() {
    font-weight: 500;
    font-size: 32px;
    line-height: 140%;
    
    /* Text [day]/Primary */
    color: $header-color;

    @media screen and (max-width: 1110px) {
        font-weight: 500;
        font-size: 28px;
        line-height: 140%;      
    }
    @media screen and (max-width: 675px) {
        font-weight: 500;
        font-size: 24px;
        line-height: 140%;
    }
}

@mixin page-caption() {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    /* Text [day]/Primary */
    color: $header-color;
    
    @media screen and (max-width: 675px) {
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
    }
}

@mixin page-text() {
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    
    /* Text [day]/Primary */
    color: $text-color;
}

@mixin page-btn() {
    cursor: pointer;
    outline: none;
    border: none;
    background: #0092ea;
    padding: 16px 20px;
    box-sizing: border-box;
    color: #fff;
    display: inline-block;

    font-weight: 500;
    font-size: 16px;
    line-height: 19px;   
    text-decoration: none; 
    transition: .3s;

    &:hover {
        background: #0081cf;
    }
    &:focus-visible,
    &:focus-within,
    &::active,
    &:target {
        background:#0064a0;
    }

    @media screen and (max-width: 675px) {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
    }
}

@mixin page-field() {
    transition: .3s;
    outline: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;

    padding: 11px 10px;
    box-sizing: border-box;
    background: #FFFFFF;
    border: 1px solid #CADEE9;   
    
    &._disabled {
        pointer-events: none;
        color: #AFB8BF;
        border: 1px solid #CADEE9;               
    }

    &._error {
        border-color: #44B8FF;
        box-shadow: 0px 0px 5px rgba(68, 184, 255, 0.2);

        & + .footer__field-clear {
            opacity: 1;
            pointer-events: all;
        }
    }

    &:focus,
    &:focus-visible {
        border: 1px solid #73BCE3;        
    }
} 